import {Sizes} from "core/ui/common/sizes"
import {ImagePos} from "core/ui/common/types"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {OnInit} from "core/ui/components/onInit"
import {Img} from "core/ui/controls"
import {Maps} from "core/utils/maps"
import {Images} from "games/observer/images"
import {WindowData} from "games/observer/logic"
import * as _ from "lodash"

const itemKey = Images.item({shape: 0, color: 0})
const imgPositions: ImagePos[] = Sizes.createImagePos([
  [Images.win_open, [0, 0]],
  [Images.win_close, [0, 0]],
  [itemKey, [22, 35]]
])

export class HouseWindowComponent extends GameComponent<WindowData> implements OnInit {
  winOpen: Img
  winClose: Img
  item: Img
  winBack: Img

  onInit(): void {
    const images = this.addImages(imgPositions)
    const get = (key: string) => Maps.getUnsafe(images, key)
    this.winClose = get(Images.win_close)
    this.winOpen = get(Images.win_open)
    this.item = get(itemKey)
  }

  onPropsUpdate() {
    var item = this.props.item
    var empty = _.isNil(item)
    this.item.visible = !empty
    this.winClose.visible = empty
    if (item) {
      this.item.key = this.gameKey(Images.item(item))
    }
  }
}




