import {Sys} from "core/logic/sys"
import {CommonAnimations} from "core/ui/animations/commonAnimations"
import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {ImagePos} from "core/ui/common/types"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {OnInit} from "core/ui/components/onInit"
import {Img} from "core/ui/controls"
import {Arrays, RandomGenerator, tuple} from "core/utils"
import {CircleSeq, UniqSeq} from "core/utils/circleSeq"
import {HouseCarComponent} from "games/observer/components/houseCarComponent"
import {HouseWindowComponent} from "games/observer/components/houseWindowComponent"
import {Images} from "games/observer/images"
import {WindowData} from "games/observer/logic"
import {Sounds} from "games/observer/sounds"
import * as _ from "lodash"

const positions = Sizes.create({
  cloudMin: 20,
  cloudMax: 80,
  light1: tuple(478, 730),
  light2: tuple(1468, 730)
})

const durations = {
  truck: 1500,
  car: 1500,
  carDelay: 5000,
  cloud: 40000,
  cloudDelay: 8000,
  hotel: 5000,
  hotelDelay: 100,
  hotelInterval: 10000,
  dayStage: 20000
}

const simpleAnimation = !Sys.isDesktop

const bg = (simpleAnimation ? [Images.bg(0)] : Images.bgList).reverse().map(x => tuple(x, tuple(0, 0)))

export class HouseComponent extends GameComponent<WindowData[]> implements OnInit {

  hotelX = Sizes.widthNormal / 2 + 305
  imagePositions: ImagePos[] = Sizes.createImagePos(bg.concat([
    ['cloud_0', [-1000, 72]],
    ['cloud_1', [-1000, 134]],
    ['cloud_2', [-1000, 182]],
    ['cloud_3', [-1000, 261]],
    ['shar', [-1000, 100]],
    ['fon_2', [0, 0]],
    ['hotel_0', [this.hotelX, 477]],
    ['hotel_1', [this.hotelX, 524]],
    ['hotel_2', [this.hotelX, 571]],
    ['hotel_3', [this.hotelX, 618]],
    ['hotel_4', [this.hotelX, 666]]
  ]))

  car1: HouseCarComponent
  car2: HouseCarComponent
  windows: HouseWindowComponent[]
  images = this.addImages(this.imagePositions)
  bgList = _.range(3).map(i => this.images.get(Images.bg(i))!)
  hotelList = Images.hotelList.map(x => this.images.get(x)!.updateFields({visible: false}))
  lights = [positions.light1, positions.light2].map(p => this.addImage([Images.light, p]).updateFields({visible: false}))
  lightsVisibla = false
  positions = Sizes.create({
    truck: [Sizes.widthNormal, 365],
    car1: [Sizes.widthNormal, 1000],
    car2: [Sizes.widthNormal, 1015]
  })

  // truck = this.add(this.createComponentWithProps(HouseCarComponent, 0)).setPos([300,300])

  onInit() {

    this.windows = windowPositions.map(([x, y]) => this.add(this.createComponent(HouseWindowComponent).updatePos(x, y)))
    this.car1 = this.add(this.createComponentWithProps(HouseCarComponent, 1)).setPos(this.positions.car1)
    this.car2 = this.add(this.createComponentWithProps(HouseCarComponent, 2)).setPos(this.positions.car2)
    this.context.uiService.mainComponent.onIntroComplete.addOnce(() => this.startAnimations())

  }

  rng = new RandomGenerator()

  startAnimations() {
    const cloudSeq = new UniqSeq(Images.cloudList.length)

    const updateCloud = (c: Img) => {
      c.key = this.gameKey(Images.cloudList[cloudSeq.next()])
      //c.y = this.rng.int(positions.cloudMax, positions.cloudMin)
    }

    Images.cloudList.forEach(x => {
      const c = this.images.get(x)!
      updateCloud(c)
      CommonAnimations.fullScreenWidthInf(c, durations.cloud, durations.cloudDelay).onLoop.add(() => {
        updateCloud(c)
      })
    })

    CommonAnimations.fullScreenWidthInf(
      this.images.get("shar")!,
      durations.cloud, durations.cloudDelay)

    this.hotelAnimation()
    if (!simpleAnimation) {
      this.bgAnimation()
    }
    const car1Tween = CommonAnimations.fullScreenWidthInf(this.car1, durations.car, durations.carDelay, false, false)
    const car2Tween = CommonAnimations.fullScreenWidthInf(this.car2, durations.car, durations.carDelay, false)
    Tweens.onPercent(car1Tween, 0, () => this.playSound(Sounds.car_01))
    Tweens.onPercent(car2Tween, 0, () => this.playSound(Sounds.car_03))

  }

  bgAnimation() {
    const [t1, ...rest] = this.bgList.map((bg, i) => {
      return Tweens.start({
        to: {alpha: 0},
        target: bg,
        duration: durations.dayStage,
        notStart: true,
        onComplete: () => {
          if (i == 1) {
            this.changeLight(true)
          }
        }
      })
    })
    t1.chain(...rest)
    t1.start()
  }

  changeLight(visible: boolean) {
    this.lightsVisibla = visible
    this.lights.forEach(x => x.visible = visible)
  }

  hotelAnimations = [
    [2, 1, 3, 0, 4],
    [0, 1, 2, 3, 4],
    [0, 4, 1, 3, 2],
    [4, 3, 2, 1, 0]
  ]

  hotelSeq = new CircleSeq(this.hotelAnimations.length)

  enableHotel(index: number) {
    const hotel = this.hotelList[index]
    hotel.visible = true
    this.delay(durations.hotel).addOnce(() => {
      hotel.visible = false
    })
  }

  hotelAnimation(index = this.hotelSeq.next()) {
    this.playSound(Sounds.neon_lamp)
    this.hotelAnimations[index].forEach((x, i) => this.delay(i * durations.hotelDelay).addOnce(() => this.enableHotel(x)))
    this.delay(durations.hotelInterval).addOnce(() => this.hotelAnimation(this.hotelSeq.next()))
  }

  updateWindows(props: WindowData[]) {
    Arrays.zipTake(this.windows, props).forEach(([x, y]) => {
      x.setProps(y)
    })
  }

  transition() {
    this.disableInput(true)
    this.playSound(Sounds.car_02)
    const truck = this.add(this.createComponentWithProps(HouseCarComponent, 0)).setPos(this.positions.truck)
    this.bringToTop(this.car1)
    this.bringToTop(this.car2)

    var updated = false
    var props = {
      target: truck, to: {x: -Sizes.width},
      duration: durations.truck,
    }
    this.delay(durations.truck * 0.45).addOnce(() => {
      this.updateWindows(this.props)
      this.disableInput(false)
    })
    Tweens.start(props).onComplete.addOnce(() => truck.destroy(true))
  }

  onPropsUpdate() {
    this.transition()
  }
}

export const windowPositions = Sizes.createArray([
  [790, 388],
  [920, 388],
  [1054, 388],
  [1184, 388],
  [790, 545],
  [920, 545],
  [1054, 545],
  [1184, 545],
  [790, 710],
  [920, 710],
  [1054, 710],
  [1184, 710],
  [790, 866],
  [920, 866],
  [1054, 866],
  [1184, 866]])




