import {ImagesMetaData} from "core/types"
import {Item} from "games/observer/logic"
import * as _ from "lodash"

export module Images {
  const carCount = 3
  const shapeCount = 3
  const colorCount = 3
  const carFonCount = 3
  export const hotelCount = 5
  export const cloudCount = 4
  export const bgCount = 4

  export function carBody(id: number) {
    return `car_body_${id}`
  }

  export function carFon(id: number) {
    return `car_fon_${id}`
  }

  export function carWheel(id: number) {
    return `car_wheel_${id}`
  }

  export function hotel(id: number) {
    return `hotel_${id}`
  }

  export function cloud(id: number) {
    return `cloud_${id}`
  }

  export function bg(id: number) {
    return `bg_${id}`
  }

  export function item(item: Item) {
    return `item_${item.shape}_${item.color}`
  }

  export const light = "light"

  const items = _.flatMap(_.range(shapeCount),
    shape => _.range(colorCount).map(color => item({shape, color})))
  const carBodies = _.range(carCount).map(carBody)
  const carWheels = _.range(carCount).map(carWheel)
  export const bgList = _.range(bgCount).map(bg)
  export const hotelList = _.range(hotelCount).map(hotel)
  export const cloudList = _.range(cloudCount).map(cloud)
  export const carFons = _.range(carFonCount).map(carFon)
  export const win_close = "win_close"
  export const win_open = "win_open"
  export const fon_2 = "fon_2"
  export const keys = [
    light,
    fon_2,
    "bg_left",
    "shar",
    win_close,
    win_open
  ].concat(
    bgList,
    cloudList,
    hotelList,
    items,
    carFons,
    carBodies,
    carWheels)

  export const data: ImagesMetaData = {
    sprites: keys,
  }

}




