import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {ImagePos} from "core/ui/common/types"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {tuple} from "core/utils"
import {UniqSeq} from "core/utils/circleSeq"
import {Images} from "games/observer/images"

const durations = {
  wheel: 250
}

interface CarProps {
  body: ImagePos
  wheels: ImagePos[]
  left: boolean
  images: ImagePos[]
}

const carFonsSeq = new UniqSeq(Images.carFons.length)

export class HouseCarComponent extends GameComponent<number> {

  cars = [
    this.createCar([184, 578, 1140, 1260], 750, 0, true, Images.carFons.map(x => tuple(x, tuple(532, 21)))),
    this.createCar([62, 339], 133, 1),
    this.createCar([105, 360], 180, 2, false)
  ]

  createCar(wheelXs: number[], wheelY: number, id: number, left = true, images: ImagePos[] = []): CarProps {
    return {
      body: [Images.carBody(id), [0, 0]],
      wheels: Sizes.createImagePos(wheelXs.map(x => tuple(Images.carWheel(id), tuple(x, wheelY)))),
      left,
      images: Sizes.createImagePos(images)
    }
  }

  onPropsUpdate() {
    const car = this.cars[this.props]
    this.addImage(car.body).angle
    const wheels = car.wheels.map(x => this.addImage(x).anchorCenter())
    wheels.forEach(w =>
      Tweens.start({
        to: {angle: (car.left ? -1 : 1) * 360},
        target: w,
        duration: durations.wheel,
        repeat: -1
      })
    )
    if (car.images.length) {
      this.addImage(car.images[carFonsSeq.next()])
    }
  }

}



