import {Signals} from "core/ui/animations/signals"
import {AnswerControlType} from "core/ui/components/answers/answerControlType"
import {BaseMainGameComponent} from "core/ui/components/games/baseMainGameComponent"
import {OnInit} from "core/ui/components/onInit"
import {Sounds} from "games/observer/sounds"
import * as Phaser from 'phaser-ce'
import {Task} from '../logic'
import * as Scene from "./houseComponent"

const durations = {
  init: 1000
}

export class MainComponent extends BaseMainGameComponent implements OnInit {
  local: Phaser.Group
  scene: Scene.HouseComponent
  answersControl = AnswerControlType.Numpad

  onInit() {
    this.scene = this.add(this.createComponent(Scene.HouseComponent))
    this.context.uiService.mainComponent.onIntroComplete.addOnce(() => this.playSound(Sounds.background_city, true))
  }

  onNewTask(task: Task) {
    if (this.isFirstTask) {
      this.scene.updateWindows(task.initWindows)
    }
    const init = this.isFirstTask
      ? this.startIntro()
      : Signals.memorize()
    init.addOnce(() =>
      this.scene.setProps(task.windows)
    )
  }

}



