import {ColorHex} from "core/types"
import {Control} from "core/ui/controls/control"

export class FigureControl extends Control {

  constructor(width: number, height: number, color: ColorHex) {
    super()
    const bmd = this.game.make.bitmapData(width, height)
    bmd.ctx.beginPath()
    bmd.ctx.rect(0, 0, width, height)
    bmd.ctx.fillStyle = color
    bmd.ctx.fill()
    const img = this.game.make.sprite(0, 0, bmd)
    this.add(img)
  }

}



