import {GameLogic, ITask} from "core/logic"
import {Arrays, RandomGenerator} from "core/utils"
import * as _ from "lodash"
import {Level} from "./index"

export interface NumberItem {
  value: number
  diff?: number
}

export interface Task extends ITask {
  numbers: NumberItem[]
  question: number
  isFirst: boolean
}

export class Logic extends GameLogic<Task, Level> {
  random = new RandomGenerator()

  prevTask: Task
  index = 0

  createNewItems(level: Level): NumberItem[] {
    return _.range(level.average, level.average + level.itemCount).map(value => ({value}))
  }

  getDiff(value: number, level: Level) {
    if (value > level.average) {

    }
    const minus = this.random.bool()
    const diff = this.random.intInclude(3, 1)
    return minus && value <= diff ? diff : (minus ? -1 : 1) * diff
  }

  nextTask(level: Level) {
    const isFirst = !this.prevTask
    const numberItems = isFirst ? this.createNewItems(level) : this.prevTask.numbers
    const numbers = numberItems.map(x => x.value)
    const indexes = _.range(numberItems.length)

    const changeCount = isFirst ? 0 : level.changeCount

    const [numbersToChange, restNumbers] = Arrays.split(_.shuffle(numbers), changeCount)
    const [min, max] = [Math.floor(level.average / 2), Math.floor(level.average * 1.5)]
    const temp = Arrays.zip(numbersToChange, this.random.uniqArray(max, changeCount, min, numbers))
    const newNumbersForReplace = new Map(temp)

    const newNumberItems = numbers.map(
      value => {
        const newValue = newNumbersForReplace.get(value)
        if (newValue) return {value: newValue, diff: (newValue - value)}
        return {value}
      }
    )

    const question = newNumberItems[this.random.sample(indexes)].value
    const task = {
      numbers: newNumberItems,
      question,
      isFirst,
      answers: [question]
    }
    this.prevTask = task
    this.index++
    return task
  }

}





