import {IGameMetaData} from "core/logic"
import {LevelOptions, ScoreOptions} from "core/types"
import {MainComponent} from "games/observer/components/mainComponent"
import {Images, Logic} from "./index"

export const gameId = "keepnumbers"

const scoreOptions: ScoreOptions = {
  time: {
    minAnswerTime: 1000,
    maxAnswerTime: 10000
  },
  scoreAnswer: 100,
  scoreBonus: 50,
  scoreBonusPerLevel: 5
}
const levelOptions: LevelOptions = {
  levels: {},
  levelsSeqCounts: []
}
export const metaData: IGameMetaData = {
  gameId: gameId,
  title: "Keep numbers",
  logic: Logic,
  component: MainComponent,
  scoreOptions,
  levelOptions,
  darkBg: false,
  images: {sprites: Images.keys},
  size: {
    width: 1280,
    height: 1280,
    heightMin: 720
  }
}



