import {CoreFonts} from "core/assets"
import {CommonDurations} from "core/logic"
import {ILastAnswer} from "core/types/logic"
import {Signals} from "core/ui/animations/signals"
import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {BaseMainGameComponent} from "core/ui/components/games/baseMainGameComponent"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {OnInit} from "core/ui/components/onInit"
import {Btn, Txt} from "core/ui/controls"
import {FigureControl} from "core/ui/controls/figureControl"
import {Arrays} from "core/utils"
import * as Phaser from 'phaser-ce'
import {Task} from './logic'

export module Images {
  export const keys = ["fon_1"]
}

const positions = Sizes.create({
  margin: 5,
  figureSize: 100
})

const Colors = ["#2B49CF", "#1BAFEF", "#47CD86", "#EFE76B", "#DC83D7"]

export class NumberList extends GameComponent<Task> {
  buttons: Btn[]

  signal = Signals.unactive()

  onAnswer(answer: ILastAnswer) {
    const btn = this.buttons.find(x => x.tag == answer.answerId)!
    const [start, end] = this.flip(btn, answer.answerId.toString())
    const [start2, end2] = this.flip(btn, "", 300)
    end.chain(start2)
    this.signal = end2.onComplete
    this.disableInputOn(this.signal)
    start.start()
  }

  private flip(btn: Btn, text: string, delay = 0) {
    return Tweens.yoyo({
      to: {symmetricScaleX: 0}, target: btn, easing: Phaser.Easing.Exponential.In,
      duration: CommonDurations.answerStatus,
      delay,
      notStart: true,
      yoyo: {
        onReturnStart: () => btn.text = text
      }
    })
  }

  onPropsUpdate() {
    this.removeAll(true)
    this.buttons = this.props.numbers.map(x => {
      const figure = new FigureControl(positions.figureSize, positions.figureSize, Colors[1])
      const txt = this.props.isFirst ? x.value.toString() : x.diff ? ((x.diff > 0 ? "+" : "") + x.diff) : "  "
      return new Btn(txt, btn => this.playerAnswer(x.value), undefined, figure).anchorCenter().setTag(x.value)
    })
    this.addChildren(this.buttons)
    Sizes.tableFromList(this.buttons, this.buttons.length, {x: positions.margin})
    if (this.props.isFirst) {
      const [start, end] = Arrays.unzip(
        this.buttons.map(x => this.flip(x, ""), 10000)
      )
      this.disableInputOn(Tweens.waitAll(end))
      Tweens.startAll(start)
    }
  }
}

export class MainComponent extends BaseMainGameComponent implements OnInit {
  local: Phaser.Group
  private question: Txt
  private numberList: NumberList

  onInit() {
    this.question = this.add(new Txt("  ", CoreFonts.textBig)).anchorCenterX().updatePos(Sizes.w2, 30)
    this.numberList = this.add(this.createComponent(NumberList))
    // this.add(this.createGameComponent(NumpadComponent)).updatePos(Sizes.width - 160, 150)
  }

  onNewTask(task: Task) {
    this.numberList.setProps(task)
    this.numberList.anchorCenter().updatePos(Sizes.w2, Sizes.h2)
    this.question.text = task.question.toString()
  }

}



